import React, { useState, useEffect, useRef } from 'react';
import { auth } from '../firebase';
import { Link, useNavigate } from 'react-router-dom';
import MobileNavbar from '../boxes/MobileNavbar';
import DropdownMenu from '../boxes/DropdownMenu';
import { Fade as Hamburger } from 'hamburger-react';


const MIN_BUTTON_WIDTH = 180; 
const DESKTOP_BREAKPOINT = 1024;

function Navbar({ onLoginButtonClick }) {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [displayName, setDisplayName] = useState('');
  const [userEmail, setUserEmail] = useState('');
  const [isNavCollapsed, setIsNavCollapsed] = useState(true);
  const [isCheckingAuth, setIsCheckingAuth] = useState(true);
  const [key, setKey] = useState(0);
  const [isSpinnerShowing, setIsSpinnerShowing] = useState(false);
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);


  const navbarRef = useRef(null);
  const dropdownRef = useRef(null);
  const navigate = useNavigate();

  const handleLogout = async () => {
    try {
      await auth.signOut();
      console.log('Signed out successfully');
      navigate('/');
    } catch (error) {
      console.error('Error signing out:', error);
    }
  };

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((user) => {
      if (user) {
        const { emailVerified, displayName: userDisplayName, email } = user;
        setIsLoggedIn(emailVerified);
        setDisplayName(userDisplayName || '');
        setUserEmail(email);
      } else {
        setIsLoggedIn(false);
        setDisplayName('');
        setUserEmail('');
      }
      setIsCheckingAuth(false);
      setKey((prevKey) => prevKey + 1);
    });

    return () => unsubscribe();
  }, [key]);

  useEffect(() => {
    function handleDocumentClick(event) {
      if (navbarRef.current && !navbarRef.current.contains(event.target)) {
        setIsNavCollapsed(true);
      }
    }

    document.addEventListener('click', handleDocumentClick);

    return () => {
      document.removeEventListener('click', handleDocumentClick);
    };
  }, []);

  const buttonMinWidth = Math.max(MIN_BUTTON_WIDTH, userEmail.length * 9);
  const buttonHeight = 42;

  const toggleNavbar = () => {
    setIsNavCollapsed(!isNavCollapsed);
  };

  const handleDropdownItemClick = () => {
    setIsNavCollapsed(true);
  };

  const isDesktop = window.innerWidth >= DESKTOP_BREAKPOINT;
  const isMobile = window.innerWidth < DESKTOP_BREAKPOINT;

  const firstName = displayName.split(' ')[0];
  
  const handleLoginButtonClick = () => {
    if (!isSpinnerShowing) {
      if (isLoggedIn) {
        setIsDropdownOpen(!isDropdownOpen); // Toggle the dropdown
      } else {
        onLoginButtonClick();
      }
    }
  };

  useEffect(() => {
    setIsSpinnerShowing(isCheckingAuth);
  }, [isCheckingAuth]);

  const buttonText = isSpinnerShowing ? (
    <div className="spinner-border " role="status">
      <span className="sr-only">Loading...</span>
    </div>
  ) : (
    isLoggedIn ? firstName : 'Login'
  );

  return (
    <nav
    ref={navbarRef}
    className={`navbar navbar-expand-lg navbar-dark menu shadow fixed-top${isNavCollapsed ? '' : ' open'}`}
  >
    <div className="container">
        <a className="navbar-brand" href="https://www.TioAssist.io/" aria-label="TioAssist Home">
          <img src={`${process.env.PUBLIC_URL}/images/logo/Logo.webp`} alt="TioAssist logo" width="180" />
        </a>

        <button
          className="navbar-toggler"
          type="button"
          onClick={toggleNavbar}
          aria-label="Toggle navigation menu"
        >
          <Hamburger size={28} color="white" toggled={!isNavCollapsed} toggle={toggleNavbar} />
        </button>

        {isMobile && (
          <MobileNavbar
            isOpen={!isNavCollapsed}
            toggleNavbar={toggleNavbar}
            onSubmitButtonClick={handleLoginButtonClick}
            isLoggedIn={isLoggedIn}
            displayName={displayName}
            handleLogout={handleLogout} 
          />
        )}

        <div className="collapse navbar-collapse justify-content-end" id="navbarNav">
          <ul className="navbar-nav">
            <li className="nav-item"><a className="nav-link" href="/#Home">Home</a></li>
            <li className="nav-item"><a className="nav-link" href="/#Let's-Chat">Let's Chat!</a></li>
            <li className="nav-item"><a className="nav-link" href="/#Testimonials">Testimonials</a></li>
            <li className="nav-item"><a className="nav-link" href="/#FAQ">FAQ</a></li>
            <li className="nav-item"><a className="nav-link" href="/#Catch-Up">Catch-Up</a></li>
            <li className="nav-item"><a className="nav-link" href="/#Contact">Contact</a></li>
          </ul>

          {isDesktop && (
          <div className="dropdown" ref={dropdownRef}>
            <div className="button-container">
              <button
                className="rounded-pill btn-rounded login-button custom-dropdown-toggle"
                type="button"
                id="loginButtonNavDropdown"
                onClick={handleLoginButtonClick}
                style={{
                  minWidth: `${buttonMinWidth}px`,
                  height: `${buttonHeight}px`,
                }}
              >
                {buttonText}
              </button>
            </div>
          </div>
        )}

        {isLoggedIn && (
          <DropdownMenu
            isOpen={isDropdownOpen} // Pass the state to the DropdownMenu component
            handleDropdownItemClick={handleDropdownItemClick}
            handleLogout={handleLogout}
          />
        )}
      </div>
      </div>
    </nav>
  );
}

export default Navbar;
