import React, { useState, lazy } from 'react';
import { createRoot } from 'react-dom/client';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import { HashRouter as Router } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import './assets/css/index.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import Navbar from './components/Navbar';
import Homesection from './components/Homesection';
import ExpertsSection from './components/ExpertSection';

const UserProfile = lazy(() => import('./Users/UserProfile'));
const LazyFooter = lazy(() => import('./components/Footer'));
const LazyPopupContainer = lazy(() => import('./boxes/PopupContainer'));

const LoadingFallback = () => <div>Loading...</div>;

const rootElement = document.getElementById('root');
const root = createRoot(rootElement);

function App() {
  return (
    <React.StrictMode>
      <Router>
        <Navbar/> 
        <Routes>
          <Route path="/" element={<Home />} />
          <Route
            path="/profile"
            element={
              <React.Suspense fallback={<LoadingFallback />}>
                <UserProfile />
              </React.Suspense>
            }
          />
        </Routes>
        {/* Render Footer and PopupContainer if they are part of your project */}
        <LazyFooter />
        <LazyPopupContainer />
      </Router>
    </React.StrictMode>
  );
}

const LazyChatSection = lazy(() => import('./components/ChatSection'));
const LazyHooverSection = lazy(() => import('./components/HooverSection'));
const LazyTestimoniesSection = lazy(() => import('./components/TestimoniesSection'));
const LazyFAQ = lazy(() => import('./components/FAQ'));
const LazyArticles = lazy(() => import('./components/Articles'));
const LazyContact = lazy(() => import('./components/Contact'));
const LazyCompaniesSection = lazy(() => import('./components/CompaniesSection'));

function Home() {
  const [showExpertSection, setShowExpertSection] = useState(false);

  const toggleExpertSection = (isVisible) => {
    setShowExpertSection(isVisible);
  };

  return (
    <>
      <Homesection />
      <LazyCompaniesSection />
      <LazyChatSection showExpertSection={showExpertSection} toggleExpertSection={toggleExpertSection} />
      {showExpertSection && <ExpertsSection />}
      <LazyHooverSection />
      <LazyTestimoniesSection />
      <LazyFAQ />
      <LazyArticles />
      <LazyContact />
    </>
  );
}

root.render(<App />);
