import React, { useState, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom'; 

function MobileNavbar({ isOpen, toggleNavbar, onSubmitButtonClick, isLoggedIn, displayName, handleLogout }) 
{  const navbarRef = useRef(null);
  const [isDropdownOpen, setDropdownOpen] = useState(false);
  const [isOverlayVisible, setOverlayVisible] = useState(false); // Add this state variable

  useEffect(() => {
    setOverlayVisible(isOpen); // Show overlay when the mobile navbar is open
  }, [isOpen]);

  const handleButtonClick = () => {
    onSubmitButtonClick(); // Call the function to open the LoginBox
    toggleNavbar(); // Close the mobile navbar
  };

  const toggleDropdown = () => {
    setDropdownOpen(!isDropdownOpen);
  };

  return (
    <>
      {isOpen && (
        <div className={`blurred-background${isOverlayVisible ? ' visible' : ''}`} onClick={toggleNavbar}></div>
      )}

    <div
      className={`mobile-navbar${isOpen ? ' open' : ''}`}
      onClick={(e) => {
        // Prevent the click event from propagating
        e.stopPropagation();
      }}
      ref={navbarRef}
    >
   <button
  type="button"
  className="btn btn-primary"
  onClick={handleButtonClick} // Call the function to open the login box and close the navbar
>
{isLoggedIn ? displayName.split(' ')[0] : 'Login'}
</button>

      <div className="SideBarMenu">
        <div className="list-item">Join as an expert</div>
        <div className="list-item">Ask questions</div>
        <div className="list-item-span">
          <div
            className={`list-item dropdown${isDropdownOpen ? ' open' : ''}`}
            onClick={toggleDropdown}
          >
          <div className="list-item-content">
  <span className="list-item-text">Dropdown</span>
  <div className="list-item-icon">
    <span className={`dropdown-arrow ${isDropdownOpen ? 'rotate-icon' : ''}`} aria-hidden="true">
      <svg width="8" height="16" viewBox="0 0 8 16" xmlns="http://www.w3.org/2000/svg">
        <path d="M0.772126 1.19065L0.153407 1.80934C0.00696973 1.95578 0.00696973 2.19322 0.153407 2.33969L5.80025 8L0.153407 13.6603C0.00696973 13.8067 0.00696973 14.0442 0.153407 14.1907L0.772126 14.8094C0.918563 14.9558 1.156 14.9558 1.30247 14.8094L7.84666 8.26519C7.99309 8.11875 7.99309 7.88131 7.84666 7.73484L1.30247 1.19065C1.156 1.04419 0.918563 1.04419 0.772126 1.19065Z"></path>
      </svg>
    </span>
  </div>
</div>

            <div className={`dropdown-content${isDropdownOpen ? ' open' : ''}`}>
              {/* Add your additional dropdown items here */}
              <div className="dropdown-item-mobile">Item 1</div>
              <div className="dropdown-item-mobile">Item 2</div>
              <div className="dropdown-item-mobile">Item 3</div>
            </div>
          </div>
        </div>
        <div role="separator" className="dropdown-divider"></div>
        {isLoggedIn && (
  <>
<Link to="/profile" 
className="profile-link"
onClick={toggleNavbar} 
>
  Profile
</Link>
<div className="signup-button logout-button">
Settings
 
  <svg xmlns="http://www.w3.org/2000/svg" color= '#212529' fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="25" width="25" >

  <path strokeLinecap="round" strokeLinejoin="round" d="M10.343 3.94c.09-.542.56-.94 1.11-.94h1.093c.55 0 1.02.398 1.11.94l.149.894c.07.424.384.764.78.93.398.164.855.142 1.205-.108l.737-.527a1.125 1.125 0 011.45.12l.773.774c.39.389.44 1.002.12 1.45l-.527.737c-.25.35-.272.806-.107 1.204.165.397.505.71.93.78l.893.15c.543.09.94.56.94 1.109v1.094c0 .55-.397 1.02-.94 1.11l-.893.149c-.425.07-.765.383-.93.78-.165.398-.143.854.107 1.204l.527.738c.32.447.269 1.06-.12 1.45l-.774.773a1.125 1.125 0 01-1.449.12l-.738-.527c-.35-.25-.806-.272-1.203-.107-.397.165-.71.505-.781.929l-.149.894c-.09.542-.56.94-1.11.94h-1.094c-.55 0-1.019-.398-1.11-.94l-.148-.894c-.071-.424-.384-.764-.781-.93-.398-.164-.854-.142-1.204.108l-.738.527c-.447.32-1.06.269-1.45-.12l-.773-.774a1.125 1.125 0 01-.12-1.45l.527-.737c.25-.35.273-.806.108-1.204-.165-.397-.505-.71-.93-.78l-.894-.15c-.542-.09-.94-.56-.94-1.109v-1.094c0-.55.398-1.02.94-1.11l.894-.149c.424-.07.765-.383.93-.78.165-.398.143-.854-.107-1.204l-.527-.738a1.125 1.125 0 01.12-1.45l.773-.773a1.125 1.125 0 011.45-.12l.737.527c.35.25.807.272 1.204.107.397-.165.71-.505.78-.929l.15-.894z" />
  <path strokeLinecap="round" strokeLinejoin="round" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
</svg>


</div>


    <div role="separator" className="dropdown-divider"></div>
    <button
      id="Logoutbtn"
      className="signup-button logout-button"
      type="button"
      onClick={() => {
        handleLogout();
        toggleNavbar(); 
      }}
    >
      Logout
    
      <svg xmlns="http://www.w3.org/2000/svg" color= '#212529' fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="25" width="25" >
  <path strokeLinecap="round" strokeLinejoin="round" d="M15.75 9V5.25A2.25 2.25 0 0013.5 3h-6a2.25 2.25 0 00-2.25 2.25v13.5A2.25 2.25 0 007.5 21h6a2.25 2.25 0 002.25-2.25V15M12 9l-3 3m0 0l3 3m-3-3h12.75" />
</svg>

    </button>
  </>
)}

      </div>
      

    </div>
  </>
  );
}

export default MobileNavbar;
